import { Component, OnInit } from '@angular/core';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { FacturasService } from '../../../providers/Api/Facturas.service';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';

@Component({
  selector: 'app-factura-configuracion',
  templateUrl: './factura-configuracion.page.html',
  styleUrls: ['./factura-configuracion.page.scss'],
})
export class FacturaConfiguracionPage implements OnInit {

  nuevo = false;
  mostrar = false;

  nit = '';
  direccion = '';
  regimen = '';
  titular = '';
  telefono: '';
  iva = false;
  ivaValor = 0;
  consecutivo = '';

  proceso = false;

  constructor(
    private almacenamiento: InicioService,
    private apiFactura: FacturasService,
    private toast: ToastServices
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.mostrar = false;
    this.consultar();
  }

  async consultar() {
      const info = await this.almacenamiento.obtenerToken();

      if ( info  ) {
        this.apiFactura.consultarConfiguracion(info.token).subscribe((data: any) => {
          if (data.exe) {
            if (!data.response) {
              this.nuevo = true;
            } else {
              // console.log(data)
              this.regimen = data.response.regimen;
              this.direccion = data.response.direccion;
              this.titular = data.response.titular;
              this.nit = data.response.nit;
              this.iva = data.response.iva;
              this.ivaValor = data.response.valorIva;
              this.consecutivo = data.response.consecutivo;

              this.mostrar = true;
            }
          } else {
            this.toast.toastError('No se pudo consultar la información de la configuración, intentelo mpas tarde');
          }
        }, (err) => {
          this.toast.toastError('No se pudo consultar la información de la configuración, intentelo mpas tarde');
        })
      }
  }


  habilitar() {
    this.nuevo = false;
    this.mostrar = true;
  }

  cambiar( event ) {
    // console.log(event);
    this.iva = event.detail.checked;
  }

  regimenc( event ) {
    this.regimen = event.detail.value;
  }

  async guardar() {
    const info = await this.almacenamiento.obtenerToken();

    if ( info  ) {
      // tslint:disable-next-line:max-line-length
      this.apiFactura.editarConfiguracion(info.token, {regimen: this.regimen, direccion: this.direccion, titular: this.titular, iva: this.iva, nit: this.nit, valorIva: this.ivaValor}).subscribe((data: any) => {
        if (data.exe) {
          this.toast.toastExitoso('Configuración actualizada');
        } else {
          this.toast.toastError('No se pudo guardar la información de la configuración, intentelo mpas tarde');
        }
      }, (err) => {
        this.toast.toastError('No se pudo guardar la información de la configuración, intentelo mpas tarde');
      })
    }
  }


}
